import NProgress from "nprogress";
import router from "./router";
// 进度条
import "nprogress/nprogress.css";

// const whitelist = ["/login", "/error/404", "/error/403"];
router.beforeEach((to, from, next) => {
  NProgress.start();
  next()
});

router.afterEach(() => {
  NProgress.done();
});
