<template>
  <v-app>
    <div style="display: flex; flex-direction: column; align-items: center">
      <notification></notification>
    </div>

    <v-app-bar app height="60px" clipped-left elevation="1">
      <v-btn icon @click="jumpHome">
        <v-icon> home </v-icon>
      </v-btn>
      <v-toolbar-title>
        <div style="display: flex; flex-direction: row; align-items: center">
          <div v-if="writeMode">
            <v-text-field v-model="title" @keyup.enter="submit"></v-text-field>
          </div>
          <div v-if="!writeMode">
            {{ this.$store.state.title }}
          </div>
          <v-btn icon @click="changeWriteMode" v-if="$store.state.titleWrite">
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
        </div>
      </v-toolbar-title>
      <v-spacer />
      <v-toolbar-items>
        <v-spacer />

        <v-row align="center" class="mx-0">
          <v-btn icon @click="back">
            <v-icon> arrow_back </v-icon>
          </v-btn>
          <v-btn icon @click="changeTheme">
            <div v-if="this.darkMode">
              <v-icon> brightness_4 </v-icon>
            </div>
            <div v-if="!this.darkMode">
              <v-icon> brightness_5 </v-icon>
            </div>
          </v-btn>
          <v-btn icon @click="$router.push('chatbot')">
            <v-icon> message </v-icon>
          </v-btn>
        </v-row>
      </v-toolbar-items>
    </v-app-bar>

    <v-main>
      <keep-alive>
        <transition mode="out-in" name="fade-transform">
          <router-view />
        </transition>
      </keep-alive>
    </v-main>
  </v-app>
</template>

<script>
import { asyncRouterMap } from "../router/routerConfig";
import Notification from "../components/Notification.vue";
import store from "@/store";
import { getTitle, postTitle } from "@/api/home_api";
import { notify } from "@/components/notification";

export default {
  name: "BasicLayout",
  components: {
    Notification,
  },
  props: {
    source: String,
  },
  data: () => ({
    drawer: true,
    mini: false,
    colorRGB: "",
    routerMaps: {
      ...asyncRouterMap,
    },
    // permission: "",
    oldPassword: "",
    newPassword: "",
    dialogContent: false,
    index: 0,
    showO: false,
    showN: false,
    darkMode: false,
    writeMode: false,
    title: "",
  }),
  created() {
    if (this.$vuetify.breakpoint.xs) {
      this.title = "选股系统";
    }
    this.setTitle();
    // this.permission = localStorage.getItem("permission");
    if (localStorage.getItem("darkMode") === undefined) {
      this.darkMode = false;
      localStorage.setItem("darkMode", false);
      this.$vuetify.theme.dark = false;
      this.$store.commit("setDarkMode", false);
    } else {
      this.darkMode = localStorage.getItem("darkMode") === "true";
      this.$vuetify.theme.dark = this.darkMode;
      this.$store.commit("setDarkMode", this.darkMode);
    }
    this.$vuetify.theme.dark = this.darkMode;
  },
  computed: {},
  methods: {
    jumpHome() {
      if (this.$route.path !== "/home") this.$router.push("/home");
      this.setTitle();
      this.$store.commit("setTitleWriteMode");
    },
    setTitle() {
      getTitle().then((res) => {
        if (res.data.status === 200) {
          store.commit("setTitle", res.data.data[0]);
          this.title = this.$store.state.title;
        } else {
          notify("error", "标题和描述数据出错");
        }
      });
    },
    back() {
      localStorage.setItem("isBackButton", "1");
      if (window.history.length <= 1) {
        this.$router.push({ path: "/" });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
    changeTheme() {
      this.darkMode = !this.darkMode;
      localStorage.setItem("darkMode", this.darkMode);
      this.$vuetify.theme.dark = this.darkMode;
      this.$store.commit("setDarkMode", this.darkMode);
    },
    changeWriteMode() {
      // this.$store.commit('setTitleWriteMode')
      this.writeMode = !this.writeMode;
    },
    submit() {
      let data = {
        title: this.title,
        describe: this.$store.state.describe,
      };
      postTitle(data).then((res) => {
        if (res.data.status === 200) {
          this.setTitle();
          this.changeWriteMode();
        } else {
          notify("error", "请求出错");
        }
      });
    },
  },
};
</script>

<style scoped>
.alert {
  position: fixed;
  top: 0;
  z-index: 9999;
}

/* fade-transform */
.fade-transform-leave-active,
.fade-transform-enter-active {
  transition: all 0.2s;
}

.fade-transform-enter {
  opacity: 0;
  transform: translateX(-30px);
}

.fade-transform-leave-to {
  opacity: 0;
  transform: translateX(30px);
}
</style>
