import { axios } from "./request";
import { URL } from "./base_url";

export function getStocksByFilter(filter) {
    return axios({
        url: `${URL}/stocks/${filter}`,
        method: "get",
    });
}

export function getFilters() {
    return axios({
        url: `${URL}/filters`,
        method: "get",
    });
}
export function getTitle() {
    return axios({
        url: `${URL}/title`,
        method: "get",
    });
}

export function postTitle(data) {
    return axios({
        url: `${URL}/title`,
        method: "post",
        data:data
    });
}