import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    type: "success",
    message: "",
    showmessage: false,
    darkMode: false,
    title: "高送转预期投资场景",
    titleWrite:true,
    item: "",
    selected: "",
    metaData: [],
    describe:"“高送转”是高送转股票的简称，指送股或转增股比例较高的一些股票。由于高送转股在送转公告发布前通常股价涨势强劲，能够使投资者短期内获得较高的投资收益，“高送转”也成为了半年报和年报出台前常见的炒作题材。 高送转发生的前提条件：公积金高、未分配利润高、净资产高；公司的公积金用于公司的亏损、扩大公司生产经营或者转为增加公司资本；所以，只有每股资本公积金高公司才具备高送转的条件。"
  },
  mutations: {
    setMetaData(state, metaData) {
      state.metaData = metaData;
    },
    setTitle(state, data) { //设置title和描述
      state.title = data.title;
      state.describe=data["describe"];
    },
    setTitle2(state, title) { //设置title和描述
      state.title = title;
    },
    setTitleWriteMode(state){
      state.titleWrite=!state.titleWrite;
    },
    setItem(state, item) {
      state.item = item;
    },
    setStock(state, stock) {
      state.stock = stock
    },
    setSelected(state, selected) {
      state.selected = selected;
    },
    alert(state, payload) {
      state.message = payload.message;
      state.type = payload.Mtype;
      state.showmessage = payload.showmessage;
    },
    removeAlert(state) {
      state.showmessage = false;
    },
    process_change(state, payload) {
      state.file_upload = payload.upload;
    },
    setDarkMode(state, darkMode) {
      state.darkMode = darkMode;
    },
    initialiseStore(state) {
      // Check if the ID exists
      if (localStorage.getItem('store')) {
        // Replace the state object with the stored item
        this.replaceState(
          Object.assign(state, JSON.parse(localStorage.getItem('store')))
        );
      }
    }
  },
  actions: {},
});

store.subscribe((mutation, state) => {
  // Store the state object as a JSON string
  localStorage.setItem('store', JSON.stringify(state));
})


// const store=Vuex.Store;
export default store;
